                                                    </tr>

                                                    <TimeTrackChildTable
                                                        v-if="isExpendCollapsed(data.trackId) && store.$state.expendListRow === true"
                                                        :key="data.trackId + 'check'"
                                                        :list="listData[data.trackId + 'abc']" v-on:closeCollapsed="expendCollapsedSections = [false]">
                                                    </TimeTrackChildTable>

                                                </template>

                                                <!-- <tr v-for="(data,i) in item.entryList" :key="i"><td>{{ data }}</td></tr> -->
                                            </tbody>
