        </div>
        <loader :isVisible="getLoadingStatus"></loader>
        <DuplicateEntry v-bind:manualData="duplicateEntry" v-if="duplicateShow" v-on:close="duplicateShow = false" />
    </section>
</template>
<style scoped> .tooltip-text {
    visibility: hidden;
    position: absolute;
    z-index: 1;
    width: 100px;
    color: white;
    font-size: 12px;
    background-color: #192733;
    border-radius: 10px;
    padding: 10px 15px 10px 15px;
}

.hover-text:hover .tooltip-text {
    visibility: visible;
}

.hover-text {
    position: relative;
    display: inline-block;
    margin: 40px;
    font-family: Arial;
    text-align: center;
}
</style>
<script>
import { defineComponent, onMounted, reactive, ref } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import { storeToRefs } from 'pinia';
import moment from "moment";
import TimeTrackChildTable from './TimeTrackChildTable.vue';
import { getPreviousDay,formatter,dateAndTimeZone,dateTimeFromat } from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService.js'
import DuplicateEntry from './modals/DuplicateEntry.vue';
import i18n from '../../localization/index'
export default defineComponent({
    components: { TimeTrackChildTable, DuplicateEntry },


    setup() {
        const store = userTimerInfo();
        const { getTimerListing, getUserInformation, getTimer, getLoadingStatus, getTimerListingFromTrackId, timerInfo, getTimerRunResponse } = storeToRefs(store)
        onMounted(() => {
            const url = `dateType=${store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)


        })

        const convertToTimeFormatHHMMSS = (seconds) => {
            if (seconds === 0 || seconds < 0) {
                return "00:00:00";
            } else {
                const hours = Math.floor(seconds / 3600);
                const minutes = Math.floor((seconds % 3600) / 60);
                const remainingSeconds = seconds % 60;

                return formatter(seconds);
            }
        };
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: 0,
            format: 0,
            date: null,
            trackId: 0,
            startTime: dateAndTimeZone(moment(),dateTimeFromat),
            stopTime: dateAndTimeZone(moment(),dateTimeFromat),
            companyId: null,
            currentDate: dateAndTimeZone(moment(),dateTimeFromat),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: getUserInformation.value.id,
            createdBy: getUserInformation.value.id,
            linkedTrackId: null,
            totalHours: 0,
            timeformat: 12,
            TagIds: null,
            isForcedUpdate: null,
            type: "TimeTrack",
            BreakDate: null,
            BreakName: null,
            BreakId: 0,
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
            BreakStopTime: dateAndTimeZone(moment(),dateTimeFromat),
            showMoreTagData: [],
            iscoutinue:false
        })
        async function startTimer(obj) {
           
            if(this.$parent.addManualTimeOn==true){
                this.$parent.addManualTimeOn=!this.$parent.addManualTimeOn
                
            }
            
            var vm = this;

            Object.assign(trackerData, obj)
            trackerData.CreatedBy = getUserInformation.value.id
            trackerData.createdBy = getUserInformation.value.id
            trackerData.isBillable = obj.billableMark=='$' ? true : false
            store.$state.loading = true
            trackerData.trackId = 0
            trackerData.totalHours = 0
            trackerData.trackAgent='web'
            trackerData.type = trackerData.trackType
            trackerData.TagIds=obj.tagData
            trackerData.dateStart =dateAndTimeZone(moment(),dateTimeFromat)
            if (trackerData.trackType == 'Break') {
                if (timerInfo.value.trackType == 'Break') {
                    this.ShowAlert(this.$t('BreakStartedAlready'), "warning", true, this.$t('Alert'));
                } else {
                    trackerData.BreakStartTime = dateAndTimeZone(moment(),dateTimeFromat)
                    trackerData.BreakStopTime = dateAndTimeZone(moment(),dateTimeFromat)
                    trackerData.BreakId = obj.breakId
                    trackerData.BreakName = obj.breakName
                    trackerData.BreakDate = dateAndTimeZone(moment(),dateTimeFromat)
                    
                    this.confirmR(this.$t("ConfirmBreakContinue"), true, (this.$t("Send") + "  " + this.$t("Records")), async function () {
                        await store.submitProjectInfo(vm.trackerData)
                        if (timerInfo.value.statusCode == "ACT" && getTimerRunResponse.value.status != 1) {
                            timerInfo.value.stopTime = dateAndTimeZone(moment(),dateTimeFromat)
                            timerInfo.value.totalHours = store.$state.timerCountInSec
                            timerInfo.value.timerStatus = 'pause'
                            timerInfo.value.createdBy = trackerData.CreatedBy
                            timerInfo.value.type = timerInfo.value.trackType
                            timerInfo.value.currentDate = dateAndTimeZone(moment(),dateTimeFromat)
                            await store.submitProjectInfo(timerInfo.value)
                            store.stopTimer()
                        }
                        vm.$emit('timerClose')
                        if (getTimer.value) {
                            setTimeout(() => {
                                vm.$emit('timerOpen')
                            }, 1);
                        }
                    })
                }

            } else {
                trackerData.startTime = dateAndTimeZone(moment(),dateTimeFromat)
                trackerData.stopTime = dateAndTimeZone(moment(),dateTimeFromat)
                if (timerInfo.value.trackType == 'TimeTrack') {
                    this.ShowAlert(this.$t('TimerStartedAlready'), "warning", true, this.$t('Alert'));
                } else {
                    this.confirmR(this.$t("ConfirmTimerContinue"), true, (this.$t("Send") + "  " + this.$t("Records")), async function () {
                        console.log("timerInfo.value.statusCode",timerInfo.value.statusCode)
                        if (timerInfo.value.statusCode) {
                            console.log("timerInfo.value.statusCode")
                            timerInfo.value.stopTime = dateAndTimeZone(moment(),dateTimeFromat)
                            timerInfo.value.totalHours = store.$state.timerCountInSec
                            timerInfo.value.timerStatus = 'stop'
                            timerInfo.value.createdBy = trackerData.CreatedBy
                            timerInfo.value.type = timerInfo.value.trackType
                            timerInfo.value.BreakStartTime = dateAndTimeZone(moment(),dateTimeFromat)
                            timerInfo.value.BreakStopTime = dateAndTimeZone(moment(),dateTimeFromat)
                            timerInfo.value.currentDate = dateAndTimeZone(moment(),dateTimeFromat)
                            timerInfo.value.iscoutinue=true
                            await store.submitProjectInfo(timerInfo.value)
                            store.breakStopTimer()
                        }
                        vm.trackerData.iscoutinue=true
                        store.submitProjectInfo(vm.trackerData)
                        vm.$emit('timerClose')
                        if (getTimer.value) {
                            setTimeout(() => {
                                vm.$emit('timerOpen')
                            }, 1);
                        }
                    })
                }
            }
            store.$state.loading = false

        }
        //Change date type and get data 
        function getListByDateType(type) {
            this.store.$state.dateFilterType = type
            const url = `dateType=${type}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)
            const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
            this.store.getTrackedtimings(url1)
        }
        //Get data from trackId and append in another table

        return {
            getTimerListing,
            convertToTimeFormatHHMMSS,
            store,
            trackerData,
            startTimer,
            getListByDateType,
            getLoadingStatus,
            getTimerListingFromTrackId,
            getPreviousDay,
            moment
        }
    },

    data() {
        return {
            collapsedSections: [false], // Each index represents a section, initialized as not collapsed
            selectedCheckboxes: [],
            expendCollapsedSections: [false],// Expend tr in table
            listData: [],
            arrayData: [],
            showAllTags: false,
            GetIndex: null,
            isActiveActionButton: false,
            tagShow: '',
            timerTags: [],
            showTagsToltip: '',
            duplicateShow: false,
            duplicateEntry: ''
        };
    },
    mounted() {
        //This script use for tags and break modal auto off when user click any other actions
        document.addEventListener("click", (e) => {
            if (this.tagShow > 0) {
                var tagsMultiselect = document.querySelector('.tagsMultiselect');
                //console.log(e.target.parentNode.closest(".tagsMultiselect"),elements)
                if (e.target == this.$refs.tags || e.target.parentNode == this.$refs.tags || e.target.parentNode.closest(".tagspanel") == e.target.parentNode  ||e.target.parentNode==e.target.parentNode.closest(".newtimetrack") ||  e.target.parentNode.closest(".newtimetrack") == this.$refs.tagsModals || e.target.parentNode.closest(".tagsMultiselect") == tagsMultiselect || e.target == this.$refs.tagsModals) return
                this.tagShow = ''
            }
            //console.log("checkk",this.showTagsToltip)
            if(this.showTagsToltip != ''){
                var tagsMultiselect = document.querySelector('.showTags');
                var more = document.querySelector('.linkcss')
                if (e.target == more || e.target.parentNode == tagsMultiselect || e.target.parentNode == e.target.parentNode.closest(".viewmoretags")  || e.target.className =='linkcss') return
                this.showTagsToltip =''
    
            }
        })
        this.store.$state.headingInactivityTab = 'ActivitiesDoneByYou'
    },
    methods: {
        async tracklocation(index, data) {
            this.expendCollapsedSections = [false]
            this.store.$state.expendListRow = true
            await this.store.GetTimerListingByTrackId(`?trackId=${data.trackId}&DateType=${this.store.$state.dateFilterType}`)
            this.arrayData[index] = this.getTimerListingFromTrackId ? this.getTimerListingFromTrackId[0].entryList : []
            this.listData = this.arrayData
            this.expendToggleCollapse(data.trackId)

        },
        arrayConvert(event) {
            if (event) {
                let convert = event.split(',')
                return convert
            }
            else {
                return []
            }
        },
        ToggleActionButton(index) {
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index == this.GetIndex) {
                this.GetIndex = ''; // Set the index if needed
            } else {
                this.GetIndex = index
            }
        },
        expendToggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.expendCollapsedSections, index, !this.expendCollapsedSections[index]);
        },
        isExpendCollapsed(index) {
            return this.expendCollapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },
        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        rowClass(rowNo) {
            let rowClass = rowNo % 2 === 0 ? "gray" : "";
            return rowClass;
        },
        tagSelectBoxOpen(id) {

            if (this.tagShow != id) {
                this.tagShow = id
            } else {
                this.tagShow = ''
            }
        },
        async saveTags(id) {
            let tagIds = ''
            this.timerTags.map((items, index) => {
                tagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            let data = `ids=${id}&ActionType=Tag&tagIds=${tagIds}`
            await this.store.UpdateOrDeleteEntries(data)
            if (this.store.tagsSaveForListingResponse) {
                const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                this.store.getTimerListing1(url)
                const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                this.store.getTrackedtimings(url1)
                this.timerTags = []
                this.tagShow = ''
            }

        },
        pushTagsData(id, data) {
            this.showTagsToltip = id
            this.showMoreTagData = []
            let tagsArray = []
            tagsArray[id] = data.splice(2, data.length)
            this.showMoreTagData = tagsArray

        },
        entryStatusClass(entryType) {

            let entryStatusClass = "";
            switch (entryType) {
                case "TRACK_ENTRY_REJECTED":
                    entryStatusClass = "delpending";
                    manualEntry = "";
                    tip = "Rejected";
                    break;
                case "TRACK_ENTRY_UPDATE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "UpdateRequestPending";
                    break;
                case "TRACK_ENTRY_DELETE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "DeleteRequestPending";
                    break;
                case "TRACK_ENTRY_ADD_MANUAL_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "AddManualRequestPending";
                    break;
                case "TRACK_ENTRY_APPROVED":
                    entryStatusClass = "lgtr-approved";
                    manualEntry = "";
                    tip = "Approved";
                    break;
                default:
                    entryStatusClass = "";
                    break;
            }
            return entryStatusClass
        },
        EntryTypeColor(track) {
            let trackBy = track.trackAgent
            let EntryTypeColor = "";
            switch (trackBy) {
                 case "Web":
                    //  EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    
                     EntryTypeColor = track.pausedHours > 0 && track.trackType=='TimeTrack'? "gt-orange-bar" : "";
                     break;
                 case "web":
                    // EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    EntryTypeColor = track.pausedHours > 0 && track.trackType=='TimeTrack'? "gt-orange-bar" : "";
                     break;
                case "Web_Manual":
                    EntryTypeColor = "high-bar";
                    break;
                case "timesheet":
                case "timesheet_mobile":
                    EntryTypeColor = "gt-manual-bar";
                    break;
                case "xamarin":
                case "linux":
                // case "silentapp":
                //     EntryTypeColor = track.PausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                //     break;
                // case "mamarinmanual":
                //     EntryTypeColor = "gt-manual-bar";
                //     break;
                // Add other cases as needed

                default:
                    // EntryTypeColor = "gt-automatic-bar";
                    EntryTypeColor="";
                    break;
            }
            return EntryTypeColor;
        },
        CSStrackBy(data) {

            let trackBy = "";
            switch (data) {
                case "desktop":
                case "linux":
                    trackBy = "fa fa-desktop font-23";
                    break;
                case "Web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "web":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "Web_Manual":
                    trackBy = "fa fa-globe font-23";
                    break;
                case "timesheet" :
                    trackBy = "fa fa-globe font-23";
                    break;
                // Add other cases as needed

                default:
                    trackBy = "fa fa-mobile font-30";
                    break;
            }
            return trackBy;
        },
        //  countinue(){
        //  this.store.submitProjectInfo(this.trackerData)
        //  }

        selectedValues() {
            //this.store.$state.selectedValues = this.selectedCheckboxes;
        },
        // async deleteChild(id) {
        //     debugger
        //     var vm=this
        //     vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("Entry")), function () {
        //     var params = `ids=${id}&childId=1`;
        //    debugger
        //      vm.store.deleteChildTimer(params)
        //     const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
        //     vm.store.getTimerListing1(url)
        //     })
        // },
        async updateIsbillable(id, ActionType) {
            var vm = this
            var params = `ids=${id}&ActionType=${ActionType}`;
            await DataService.setAction(params).then((response) => {
                debugger
                if (response.data == "RecordUpdatedSuccessfully") {
                    vm.ShowAlert(i18n.t('RecordUpdatedSuccessfully'), "success", true, i18n.t('Alert'));

                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                    const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                    this.store.getTrackedtimings(url1)
                }
                else {
                    vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                }
                //var data=response

            });
        },
        async duplicate(obj, index) {
            var vm = this
            vm.confirmR(i18n.t("ConfirmDuplicateEntry"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                vm.duplicateEntry = obj
                console.log("duplicate",vm.duplicateEntry);
                vm.duplicateShow = vm.duplicateShow = !vm.duplicateShow
            })
        },
        UpdateOrDeleteEntries(id) {
            var vm = this
            var params = `ids=${id}&ActionType=Delete`;
            vm.confirmR(i18n.t("ConfirmDelete"), true, (i18n.t("Delete") + "  " + i18n.t("entry")), function () {
                DataService.setAction(params).then((response) => {

                    if (response.data == "DeletedSuccessfully") {
                        vm.ShowAlert(i18n.t('DeletedSuccessfully'), "success", true, i18n.t('Alert'));

                        const url = `dateType=${vm.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                        vm.store.getTimerListing1(url)
                        const url1 = `dateType=${vm.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                        vm.store.getTrackedtimings(url1)
                    }
                    else {
                        vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                    }
                    // const url = `dateType=${this.dateType}&search=&toDate=&fromDate=&newTrackId=0`
                    // this.store.getTimerListing1(url)
                });
            });
        },
    }
})
